.instagram-profile {
    max-width: 935px;
    margin: 0 auto;
    padding: 20px;
}

.profile-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0;
    border-bottom: 1px solid #dbdbdb;
}

.profile-main {
    padding: 30px 0;
}

.profile-top {
    display: flex;
    gap: 80px;
    margin-bottom: 44px;
}

.profile-avatar {
    position: relative;
    width: 150px;
    height: 150px;
}

.profile-avatar img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
}

.avatar-upload-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    opacity: 0;
    transition: opacity 0.2s;
}

.avatar-upload-overlay:hover {
    opacity: 1;
}

.upload-label {
    color: white;
    font-size: 24px;
    display: flex;
    justify-content: center;
    align-items: center;

}

.profile-info-section {
    flex: 1;
}

.profile-header-row {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-bottom: 20px;
}

.profile-button {
    padding: 5px 9px;
    background: rgba(0, 132, 255, 0.644);
    border: 1px solid #dbdbdb;
    border-radius: 4px;
    font-weight: 600;
    cursor: pointer;
}

.profile-stats {
    display: flex;
    gap: 40px;
    margin-bottom: 20px;
}

.stat {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.stat-value {
    font-weight: 600;
}

.stat-label {
    color: #8e8e8e;
}

.profile-bio-section {
    margin-bottom: 30px;
}

.bio-text {
    margin-bottom: 10px;
    white-space: pre-wrap;
}

.interests-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
}

.interest-tag {
    color: #00376b;
    font-size: 14px;
}

.edit-fields {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.edit-input {
    padding: 8px;
    border: 1px solid #dbdbdb;
    border-radius: 4px;
}

.groups-section {
    border-top: 1px solid #dbdbdb;
    padding-top: 20px;
}

.groups-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
    margin-top: 20px;
}

.group-card {
    border: 1px solid #dbdbdb;
    border-radius: 4px;
    padding: 15px;
    text-align: center;
}

.join-button {
    width: 100%;
    padding: 8px;
    border: none;
    border-radius: 4px;
    background: #0095f6;
    color: white;
    font-weight: 600;
    cursor: pointer;
}

.join-button.joined {
    background: #dbdbdb;
}

.login-message {
    text-align: center;
    padding: 40px;
    color: #8e8e8e;
}
.avatar-upload-input{
  width: 100%;
  height: 100%;
  border-radius: 50%;
  opacity: 1;
}
.fa-camera{
  opacity: 2;
  z-index: 100;
  margin: auto;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;

  position: absolute;
}
/* Mobile Responsive Styles */
@media screen and (max-width: 768px) {
    .instagram-profile {
        padding: 0;
    }

    .profile-header {
        padding: 10px 16px;
        position: sticky;
        top: 0;
        background: white;
        z-index: 100;
    }

    .profile-header h1 {
        font-size: 1.2rem;
    }

    .profile-main {
        padding: 16px;
    }

    .profile-top {
        flex-direction: column;
        gap: 15px;
        align-items: center;
        margin-bottom: 24px;
    }

    .profile-avatar {
        width: 96px;
        height: 96px;
    }

    .profile-info-section {
        width: 100%;
        text-align: center;
    }

    .profile-header-row {
        flex-direction: column;
        gap: 12px;
        margin-bottom: 16px;
    }

    .profile-button {
        width: 100%;
        padding: 7px 16px;
    }

    .profile-stats {
        justify-content: center;
        padding: 12px 0;
        border-top: 1px solid #dbdbdb;
        border-bottom: 1px solid #dbdbdb;
        margin: 0 -16px 16px;
    }

    .stat {
        flex-direction: row;
        gap: 4px;
    }

    .stat-value {
        font-weight: 600;
        margin-right: 4px;
    }

    .profile-bio-section {
        text-align: left;
    }

    .edit-fields {
        gap: 12px;
    }

    .edit-input {
        padding: 10px;
        font-size: 16px; /* Prevents zoom on iOS */
    }

    .groups-section {
        margin: 0 -16px;
        padding: 16px;
    }

    .groups-section h3 {
        padding-left: 16px;
    }

    .groups-grid {
        grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
        gap: 12px;
        padding: 0 16px;
    }

    .group-card {
        padding: 12px;
    }

    /* Mobile-specific edit mode */
    .edit-fields {
        margin-top: 16px;
    }

    .edit-input {
        margin-bottom: 8px;
    }

    /* Mobile-friendly interests tags */
    .interests-tags {
        margin-top: 8px;
        gap: 6px;
    }

    .interest-tag {
        font-size: 13px;
        background: #fafafa;
        padding: 4px 8px;
        border-radius: 4px;
    }
}

/* Additional styles for very small screens */
@media screen and (max-width: 375px) {
    .profile-stats {
        gap: 20px;
    }

    .groups-grid {
        grid-template-columns: 1fr;
    }
}

/* Fix for iOS input zoom */
@media screen and (max-width: 768px) {
    input[type="text"],
    input[type="date"],
    textarea {
        font-size: 16px;
    }
}

/* Add smooth scrolling for iOS */
.instagram-profile {
    -webkit-overflow-scrolling: touch;
}
