/* General Calendar Styles */
.react-calendar {
    width: 100%;
    max-width: 350px;
    background: none;
    border: none;
    font-family: Arial, sans-serif;
    margin-bottom: 10px;
}

.react-calendar__tile {
    padding: 10px;
    border-radius: 10px;
    text-align: center;
    font-size: 16px;
    background: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.react-calendar__tile--now {
    background: rgba(255, 11, 11, 0.911); /* Highlight today */
}

.react-calendar__tile--active {
    background: linear-gradient(135deg, #769aff5e, #00d9ff9c);
    color: white;
    border-radius: 10px;
}

/* Attendance Colors */
.tile-present{
    background: linear-gradient(135deg, #4caf50, #66bb6a); /* Green for present */
    color: white;
}

.tile-absent{
    background: linear-gradient(135deg, #f44336, #ef5350); /* Red for absent */
    color: white;
}

.tile-maybe {
    background: linear-gradient(135deg, #ff9800, #ffb74d); /* Orange for maybe */
    color: white;
}

.holiday {
    background: linear-gradient(135deg, #f3d4219c, rgba(226, 147, 44, 0.479));
    /* Blue for holidays */
    color: white;
}
.sunday{
    background: linear-gradient(135deg, #b676ff86, #0044ff42);
    color: white;
}

.react-calendar__month-view__days__day--neighboringMonth {
    color: rgba(0, 0, 0, 0.5); /* Faded days from previous/next month */
}

/* Attendance Buttons */
.attendance-options {
    margin-top: 10px;
    display: flex;
    gap: 10px;
}

.attendance-options button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    color: white;
    transition: background-color 0.3s ease;
}

.present-h3{
    /* background-color: #4caf50; */
    color: #04aa09;
}

.absent-h3{
    /* background-color: #f44336; */
    color:#f44336 ;
}

.holiday-h3{
    /* background-color: #ff9800; */
    color: #ff9800;

}

.present-btn:hover {
    background-color: #66bb6a;
}

.absent-btn:hover {
    background-color: #ef5350;
}

.maybe-btn:hover {
    background-color: #ffb74d;
}
.attendance-summary{
  margin-bottom: 100px;
}
.attendance-summary ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    width: 300px;  
}

.attendance-summary li {
    display: flex;
    margin-top: 5px;
    margin-bottom: 5px;
}

/* Align the status to the left side */
.Status {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-grow: 1; /* Take up available space */
    font-size: 16px;
    color: #333;
    
}
.alert-box{
   
    border:solid rgba(185, 185, 185, 0.308) 2px ;
