/* Basic styling for the navbar */
.navbar {
    background-color: #fff; /* White background */
    padding: 5px 0; /* Padding around the navbar */
    color: #000; /* Black text color */
    font-family: 'Arial', sans-serif; /* Font styling */
    position: sticky;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    border-top: 1px solid #ddd; /* Light gray border on top */
    z-index: 1000;

}

/* Unordered list that holds the navbar items */
.navbar ul {
    list-style: none; /* Remove bullet points */
    display: flex; /* Flexbox layout */
    justify-content: space-around; /* Even spacing between items */
    align-items: center;
    margin: 0;
    padding: 0;
}
a{
    color: #000;
}


/* Individual navbar list items */
.navbar ul li {
    cursor: pointer; /* Makes the items clickable */
    padding: 10px 15px; /* Padding inside each item */
    font-size: 18px; /* Font size */
    border: none; /* No border */
    transition: color 0.3s, border 0.3s; /* Smooth hover effects */
}

/* Hover effect for navbar items */
.navbar ul li:hover {
    color: #888; /* Light gray on hover */
}

/* Active/Selected navbar item */
.navbar ul li.active {
    color: #000; /* Darker color for active state */
}

/* Icons inside the navbar */
.navbar ul li svg {
    display: block;
    margin: 0 auto;
}

/* Media query for mobile screens */
@media (max-width: 600px) {
    .navbar ul {
        flex-direction: row; /* Keep items in a row */
    }

    .navbar ul li {
        flex-grow: 1; /* Make each item take up equal space */
        text-align: center; /* Center the icons */
    }
}
